<template>
  <div>
    <v-container>
      <v-layout row>
        <v-flex md6 offset-md3>
          <v-card>
            <v-toolbar dark color='blue'>
              <v-toolbar-title>Input Data Bobot</v-toolbar-title>
            </v-toolbar>
            <v-card-text>
              <v-form>
                <v-text-field
                  label="Ketrampilan"
                  type="number"
                  min="0"
                  max="1"
                  step="0.01"
                  v-model="weights.trampil"
                >
                </v-text-field>
                <v-text-field
                  label="Pengetahuan"
                  type="number"
                  min="0"
                  step="0.01"
                  max="1"
                  v-model="weights.pengetahuan"
                >
                </v-text-field>
                <v-text-field
                  label="Nilai Seluruh Mata Pelajaran"
                  type="number"
                  min="0"
                  v-model="weights.nilaiSeluruh"
                >
                </v-text-field>
                <v-text-field
                  label="Nilai Sikap"
                  type="number"
                  min="0"
                  max="1"
                  step="0.01"
                  v-model="weights.sikap"
                >
                </v-text-field>
                <v-text-field
                  label="Ekstrakurikuler"
                  type="number"
                  min="0"
                  max="1"
                  step="0.01"
                  v-model="weights.ekstra"
                >
                </v-text-field>

                <v-btn dark color='blue' @click="run">
                  Jalankan
                </v-btn>
              </v-form>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    <v-card>

    </v-card>
    <v-card>
      <v-card-title>
        <v-spacer/>
      </v-card-title>
      <v-card-text>
        <v-data-table
          :headers="viewportAwareHeaders"
          :items="items"
          :total-items="$vuetify.breakpoint.mdAndUp ? items.length : undefined"
          id="main-table"
          hide-actions
        >
          <template slot="items" slot-scope="props">
            <tr v-bind:class="{ top: (props.index < 10) }">
              <td>{{ props.item.name }}</td>
              <td>{{ props.item.kelas }}</td>
              <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rankKelas }}</td>
              <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rataSikap }}</td>
              <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rataPengetahuan }}</td>
              <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rataTrampil }}</td>
              <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.rataSeluruh }}</td>
              <td v-if="$vuetify.breakpoint.smAndUp" class="text-xs-center">{{ props.item.ekstra }}</td>
              <td class="text-xs-center">{{ parseFloat(props.item.rank).toFixed(4) }}</td>
            </tr>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import { Group } from '@/services'
import { mapGetters } from 'vuex'

export default {
  name: 'SmartView',
  data () {
    return {
      headers: [
        {
          text: 'Nama',
          value: 'name'
        },
        {
          text: 'KLS',
          value: 'kelas'
        },
        {
          text: 'RANK. KLS',
          value: 'rankKelas',
          align: 'center'
        },
        {
          text: 'R.SIKAP',
          value: 'rataSikap',
          align: 'center'
        },
        {
          text: 'R.PTH',
          value: 'rataPengetahuan',
          align: 'center'
        },
        {
          text: 'R.TRAMPIL',
          value: 'rataTrampil',
          align: 'center'
        },
        {
          text: 'R.SELURUH',
          value: 'rataSeluruh',
          align: 'center'
        },
        {
          text: 'EKS',
          value: 'ekstra',
          align: 'center'
        },
        {
          text: 'SMART',
          value: 'rank',
          align: 'center'
        }
      ],
      items: [],
      weights: {
        sikap: 0.5,
        pengetahuan: 0.2,
        nilaiSeluruh: 0.15,
        trampil: 0.1,
        ekstra: 0.05
      }
    }
  },
  computed: {
    ...mapGetters([
      'activeGroup'
    ]),
    viewportAwareHeaders () {
      if (this.$vuetify.breakpoint.smAndUp) {
        return this.headers
      }
      return this.headers.filter((v, i) => {
        return i === 0 || i === 1 || i === 8
      })
    }
  },
  methods: {
    async reload () {
      let items = await Group.smart(this.activeGroup.id)
      this.items = items
    },
    async run () {
      const weights = [
        this.weights.sikap,
        this.weights.pengetahuan,
        this.weights.nilaiSeluruh,
        this.weights.trampil,
        this.weights.ekstra
      ]
      const gid = this.activeGroup.id

      const result = await Group.sensitivity(gid, weights)
      this.items = result
    }
  },
  async mounted () {
  }
}
</script>

<style>
#main-table tr.top > td {
  font-weight: bold;
}
</style>
